a:visited,
a{ color:#fff; }
button{ cursor:pointer; }
/*
  App.css
  - by Chris DeFreitas, chrisd@europa.com
  - top level application styles for GuitrJoe app
  
*/

.App {
  color:white; height:100vh; width:100vw;
  text-align:center;
  z-index:1;
}
.App-header {
  background-color: #282c34; 
  border-bottom:1px solid #ffffff30;
  /* min-height: 2vh;  */
  display: flex;
  padding:0 0 0 1em;
  font-size: 1.5em;
  /* flex-direction: column; */
  align-items: left;
  justify-content: left;
}
.header{ font-family:'Fuggles'; font-size:2.5em;  text-shadow:2px 2px 3px #000; position:relative; }
.Logo{ height:0.8em; position:relative; top:8px; left:-5px; }

.btnInfo{ background-color:#282c34; border:1px #eee solid; border-radius:5px; color:#fff; 
  height:2em; padding:0 1em; position:absolute; top:15px; right:15em; 
}


/*
  Fretboard.css
  - by Chris DeFreitas, chrisd@europa.com
  - styles for Fretboard and QueryPnl
  
*/

.fretboard{ display:inline-table;  margin:0 auto 0 auto; position:relative; }
.fretboard[id="Fretboard0"]{ margin:1em auto 0 auto;}

.queryPnl{ color:#ffffffd0; position:relative; margin:0 auto;   text-align:center; 
  border:none; outline:none; padding:0; top:-0.5em;
}
.queryControls{ background-color:hsl(177deg 68% 31% / 92%); display:inline-table; position:relative; 
  border: 1px solid #333; border-radius:5px; box-shadow:5px 5px 15px 1px #0000007a;
  margin:0; padding:0 0.5em 0.5em 0.5em;
  height:3em; width:-webkit-fit-content; width:-moz-fit-content; width:fit-content; top:0px;
}
.dataPnl { border:none; display:inline-table; padding:0 0.5em; text-align:center;  }
.dataPnl label{ color:#ffffffd0; font-weight:bold; font-size:1em; text-shadow:1px 1px 5px #00000090; }
.dataPnl label[data-selected="true"]{ cursor:pointer; }
.dataPnl select{ background-color:#ffffffa0; border:none; border-radius:3px; cursor:pointer;
  display:block; margin:0 auto; padding:5px 0;
  min-width:5em;  max-width:10em; 
  outline:0;  text-align-last:center;
}
/*.dataPnl select option{}   not applicable because controlled by OS */

.qryCollapsed .qryBtnCollapse,
.qryCollapsed .queryControls,
.qryCollapsed .qryBtnDupe,
.qryCollapsed .qryBtnDel,
.qryCollapsed .qryBtnClear{ display:none; }

.qryBtn{ background-color:hsl(177deg 68% 31% / 92%); border:1px outset #0003; border-radius:5px; 
  box-shadow: 5px 5px 15px 1px #0000007a; cursor:pointer; 
  display:inline-table; margin:0 0.25em; top:20px; height:1.5em; width:1.5em; position:relative; 
  color:#000; font-size:1.25em; font-weight:normal; text-shadow:1px 1px 1px #fff6; }
.qryBtnCollapse div{ -webkit-transform:rotateZ(-90deg); transform:rotateZ(-90deg); text-shadow:-1px 1px 1px #fff6; 
  position:relative; top:0px; left:-2px; width:100%;  }

/* .infoPnl{ } */
.qryExpanded .infoPnl{ display:block; }
.qryBtnCollapse .infoPnl{ display:inline-table; }
.infoDiv{ display:inline-table; margin:5px 0 0 0.5em; text-shadow:1px 1px 3px #000;}
.infoDiv .propName{ font-weight:bold; margin:0 0 0 0.5em;}
.infoDiv .propName:first-child{ margin:0;}
.infoDiv .ivl{ cursor:pointer; font-weight:normal; }
.infoDiv .ivl[data-selected="noteFilter"] { background:#aaf; border-radius: 3px; }  /* note in props.noteFilter */
.infoDiv .ivl sub{ font-size:0.6em; position:relative; left:-3px; }
.qryBtnExpand{ display:inline-table; position:sticky; height:1em; width:1em; }
.qryBtnExpand div{ position:relative; -webkit-transform:rotateZ(90deg); transform:rotateZ(90deg); height:1em; left:3px; top:2px;
  font-size:0.8em; text-shadow:1px -1px 1px #fff6; }

.btnFilter{ position:absolute; width:20px;
  cursor:pointer; 
  /* background-color:transparent; border:0; outline:0;  */
  height:2em; width:48%; margin:0;
  background:linear-gradient(-25deg, #fff,#eec 50%,#bba 100%);
  border:2px outset #ffffff90;  border-radius:5px; box-shadow:-2px -2px 5px #00000080;
}
.btnFilter div{ color:#177d7f; background:transparent; border:none; outline:0;
  font-size:2.5em; position:relative;  left:-2px; top:-14px;
}
.btnFretFilter{ width:80%; margin:0 auto; }

.btnStrgFltr{
   /* background:linear-gradient(-90deg, #fff,#eec 50%,#bba 100%); */
  background:transparent; border:none; outline:0; box-shadow:none;
  top:6px; left:0px; height:24px; width:25px; z-index:100;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
.btnStrgFltr img{ position:relative; height:10px; top:0px; left:0px; }
.btnStrgFltr2{  top:22px; }
.btnStrgFltr3{  top:22px; }
.btnStrgFltr4{  top:22px; }
.btnStrgFltr5{  top:22px; }
.btnStrgFltr6{  top:22px; }

.fretButton{ 
  color:#000; border:none; border-radius:5px;  box-shadow:1px 1px 5px #0007;
  text-align:left; margin:0; padding-left:10px;
  position:absolute; top:5px; left:15px; height:2em; width:4.5em;
  z-index:100;
}
.fretButton{ background:linear-gradient(180deg, #fff,#eec 50%,hsl(60deg 5% 36%) 100%); }
.fretButton[data-selected="fretRoot"] { border: 1px #04ff00 solid; }
.fretButton[data-selected="selNote"] { border: 1px #fffb00 solid;  }
/* .fretButton[data-selected="0"] {} */
.fretButton[data-selected="1"] { background:#867171; }
.fretButton[data-selected="noteFilter"] { background:#aaf; }  /* note found in props.noteFilter */
.fret0 .fretButton { left:39px; }
.fretButton sup{ color:#0f000fa0; font-size:0.7em; position:absolute; top:0px; text-shadow:none;  }
.fretButton sub{ color:#00000050; text-shadow:none; position:relative; }
.fretButton .subInterval{ cursor:alias; top:0px; right:-10px; }
.fretButton .spanNote .subOctave{ color:#0008; font-size:0.6em; }
.fretButton .spanIvl span{ color:#0007; font-size:0.7em; }
.fretButton .subNote{ cursor:alias; top:0px; right:-10px; }
/* .fretButton .subNote .subOctave{ color:red; } */
.strg2 .fretButton,
.strg3 .fretButton,
.strg4 .fretButton{ top:calc( 1em + 6px) }
.strg5 .fretButton,
.strg6 .fretButton{ top:calc( 1em + 7px) }

/* .fretButton[data-ivl="P1"],  */
/* .fretButton[data-ivl="P1"] sub, */
.fretButton[data-ivl="P1"] sup{ color: #fff; text-shadow:1px 1px 1px #441616; }
.fretButton[data-ivl="m3"] .supInterval,
.fretButton[data-ivl="M3"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000; }
.fretButton[data-ivl="P5"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000; }
.fretButton[data-ivl="m7"] .supInterval,
.fretButton[data-ivl="M7"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000;  }

/*
 FretPnl.css
  - by Chris DeFreitas, chrisd@europa.com
  - styles to manage construction of Fretboard frets and strings etc
*/

.fretPnl{ border:0; border-collapse:separate; border-spacing:0px; table-layout:fixed;
  padding:0 0 0 10px; margin:0 auto;
  position:relative; width:auto;
}
.fretPnl tr{ 
  border:0;
}
.fretPnl tr td{   /* default cells */
  background-color:#3d3b4fff; border:0; padding:0; 
  height:2em; max-height:2em; width:5em; min-width:5em; position:relative;
}
.fretPnl .strg .fret{ cursor:pointer; }   /* all fret cells */
 
.fretPnl .frameTop td[data-fretfilter="true"]{      /* filtered fret numbers */
  color:#3c31a7; 
}
.fretPnl tr .fret[data-fretfilter="true"] .fretbar{      /* filtered fretbar */
  background:#3a3a3a; 
}
.fretPnl tr .fret[data-fretfilter="true"]{      /* filtered frets */
  cursor:default;
}
.fretbar{
  width:1px; height:100%;
  background-color:#999;
  border:0;
  border-right:1px solid #335;
  float:right;
}
.fretPnl tr .fret0{     /* fret 0 */
  background-color:transparent; border:0; width:7em; min-width:7em;
}
.fretPnl tr .fret0 .fretbar{      /* bar */
  width:7px; 
  background-color:#6f6f7f;
}
.fretPnl .frame .fret1{  /* hide bar in frames */
  background-color:transparent; border:0;
}
.fretPnl .strg1 .tdBorder0{  /*  */
  border:0;
}
.fretPnl tr .borderRight,
.fretPnl .frame .borderBottom,
.fretPnl .frame .borderRight{      /* format right border  */
  background-color:transparent; width:0.5em;  min-width:0.5em;
}
.fretPnl .borderBottom .borderRight{
  border-bottom:0;
}
.fretPnl tr td:last-child{ /* end of board */
  border-right:0; width:1.5em;
}

.fretPnl .strg1 td{ height:1em;  }    /*  height of top/bottom edge  */
.fretPnl .borderBottom td{ cursor:pointer; height:1.25em; }

.fretPnl .frame td{    /* format td's in frame and border rows */
  background-color:transparent; cursor:pointer; 
  color:#ffffff80;
  font-size:1em;
  text-shadow:1px 1px 5px #0008;
  text-align:center;
  height:0.5em;
}
.fretPnl .frame td .collapsed{    /* show a bar every third fret when qry.collpased=true */
  background-color:#71b578;
  height:3px;
  margin-top:0.5em;
}
.fretPnl .frameBottom .tdBottom{ position:relative; }

/* strings */
.fretPnl tr[data-fltr="true"] .stringdiv{ background-color:#ffffff20; }
.fretPnl .stringdiv{ background-color:#fffffff0;  
  position:absolute; top:100%; left:0px;
  height:1px; width:100%; z-index:99;
}
.fretPnl .fret14 .stringdiv{ width:120%;  }
.fretPnl .strg3 .stringdiv,
.fretPnl .strg4 .stringdiv,
.fretPnl .strg5 .stringdiv{ height:2px;  }
.fretPnl .strg6 .stringdiv{ height:2.5px;  }



/*
  Dialog.css
  - custom style for reactjs-popup
  - for GuitarJoe by Chris DeFreitas

*/

.popup-content{background:#282c34; border:3px solid #7771; border-radius:15px;
  color:#fff; 
  margin:auto; padding:0 1em; max-width:40em;
}[role=tooltip]
.popup-content{box-shadow:0 0 3px rgba(0,0,0,.16);border-radius:5px}
.popup-overlay{background:rgba(0,0,0,.5)}[data-popup=tooltip]
.popup-overlay{background:transparent}
.popup-arrow{-webkit-filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16)); filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));color:#fff;
  stroke-width:1px;stroke:#d7d7d7;stroke-dasharray:30px; stroke-dashoffset:-54px;
  left:0;right:0;top:0;bottom:0
 }

 /* modal dialog */
.modal{ font-size:0.9em; }
.modal > .header { width: 100%;
  border-bottom: 1px solid gray;
  /* font-size: 1.5em; */
  text-align: center;
  /* padding: 5px; */
}
.modal > .content { width:auto; padding:0.5em 1em; }
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}

